import { createSlice } from '@reduxjs/toolkit'
import { Donors } from "./entities/Donors";

export const donorSlice = createSlice({
    name: 'Donors',
    initialState: Donors,
    reducers: {
        updateDonorsList: (state, { payload }) => {
            const { bloodType, list } = payload;
            state[bloodType] = { ...state[bloodType], ...list }
        },
    },
})

export const { updateDonorsList } = donorSlice.actions

export default donorSlice.reducer