import loadingIcon from "./../assets/images/loading.gif"

const Loader = ({ className, inline }) => {

    if (inline)
        return (
            <div className={"m-0 p-0 flex flex-col items-center justify-center " + className}>
                <img className="rounded-full w-40 h-40" src={loadingIcon} alt="Loading" />
                <div className="text-gray-800 mt-3">Loading... Please wait</div>
            </div>
        );

    return (
        <div className="m-0 p-0 h-screen w-full flex flex-col items-center justify-center">
            <img className="rounded-full w-40 h-40" src={loadingIcon} alt="Loading" />
            <div className="text-gray-800 mt-3">Loading... Please wait</div>
        </div>
    );
}

export default Loader;