import { createSlice } from '@reduxjs/toolkit'
import { User } from './entities/User'

export const authSlice = createSlice({
    name: 'Auth',
    initialState: User,
    reducers: {
        setUserData: (state, { payload }) => {
            if (payload.id) {
                state.id = payload.id
                state.name = payload.name
                state.image = payload.image
                state.phone = payload.phone
                state.email = payload.email
                state.emailVerified = payload.emailVerified
                state.isLoggedIn = true
            } else {
                return { ...User, isLoggedIn: false }
            }
        },
        updateUserData: (state, { payload }) => {
            const REQUIRED_DETAILS = ["location", "bloodGroup", "gender", "dob"]
            const fields = Object.keys(payload);
            state.isUpdateRequired = !REQUIRED_DETAILS.every(e => fields.includes(e));
            state.location = payload?.location;
            state.bloodGroup = payload?.bloodGroup;
            state.gender = payload?.gender;
            state.dob = payload?.dob;
            state.reputation = payload?.reputation;
            state.name = payload.name
            state.image = payload.image
        },
    },
})

export const { setUserData, updateUserData } = authSlice.actions

export default authSlice.reducer