import { createSlice } from '@reduxjs/toolkit'
import { Requests } from "./entities/Requests";

export const requestSlice = createSlice({
    name: 'Requests',
    initialState: Requests,
    reducers: {
        setRequests: (state, { payload }) => {
            const obj = {};
            for (const request of payload) {
                obj[request.id] = request
            }
            return obj;
        },
        updateRequests: (state, { payload }) => {
            const { id } = payload
            if (id in state)
                state[id] = { ...state[id], ...payload }
            else
                state[id] = payload
        },
        updateDonors: (state, { payload }) => {
            const { requestId, donors } = payload
            if (requestId in state) {
                state[requestId]["donors"] = donors
                state[requestId]["unitsGot"] = donors.reduce((prev, curr) => prev + curr.units, 0)
            }
        }
    },
})

export const { setRequests, updateRequests, updateDonors } = requestSlice.actions

export default requestSlice.reducer